*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

@media only screen and (width <= 51rem) {
  html {
    font-size: 50%;
  }
}

body {
  background-color: #03012d;
}

.container {
  color: #fff;
  grid-template-rows: min-content minmax(25rem, min-content) repeat(2, min-content);
  grid-template-columns: [full-satrt] minmax(4rem, 1fr)[center-start] repeat(7, [col-start] minmax(min-content, 14rem)[col-end])[center-end] minmax(4rem, 1fr)[full-end];
  gap: 3.2rem 2.4rem;
  margin-bottom: 4.8rem;
  display: grid;
}

@media only screen and (width <= 68em) {
  .container {
    row-gap: 1.6rem;
  }
}

@media only screen and (width <= 37.5rem) {
  .container {
    grid-template-columns: [full-satrt] minmax(2rem, 1fr)[center-start] repeat(7, [col-start] minmax(min-content, 14rem)[col-end])[center-end] minmax(2rem, 1fr)[full-end];
  }
}

body {
  color: #d5d4d9;
  font-family: Bricolage Grotesque, sans-serif;
  font-size: 1.8rem;
  font-weight: 300;
  line-height: 1.6;
  position: relative;
}

.heading-primary {
  font-size: 4.4rem;
}

@media only screen and (width <= 51rem) {
  .heading-primary {
    font-size: 3rem;
  }
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes load {
  0% {
    transform: scale(0);
  }

  25% {
    transform: translateY(.5px);
  }

  50% {
    transform: translateY(1px);
  }

  100% {
    transform: translateX(1.2px);
  }
}

.center-text {
  text-align: center;
}

.mb-large {
  margin-bottom: 6.4rem;
}

.mb-medium {
  margin-bottom: 4.8rem;
}

@media only screen and (width <= 51rem) {
  .mb-medium {
    margin-bottom: 3.2rem;
  }
}

.dropdown {
  font-family: DM Sans, sans-serif;
  font-size: 1.6rem;
  position: relative;
}

.dropdown__title {
  color: currentColor;
  background-color: #312f4b;
  border-radius: 9px;
  justify-content: space-evenly;
  align-items: center;
  gap: .8rem;
  margin-bottom: .4rem;
  padding: .8rem;
  display: flex;
}

.dropdown__button {
  cursor: pointer;
  background-color: #312f4b;
  border: none;
  align-self: flex-end;
}

.dropdown__icon {
  fill: #fff;
  width: 2rem;
  height: 2rem;
}

.dropdown__menu {
  background-color: #312f4b;
  border-radius: 9px;
  flex-direction: column;
  gap: .8rem;
  width: 20rem;
  padding: 1.2rem;
  font-size: 1.8rem;
  list-style: none;
  display: flex;
  position: absolute;
  top: 100%;
  right: 0;
}

.dropdown__menu--setting {
  z-index: 2;
}

.dropdown__menu--fav {
  z-index: 1000;
}

.dropdown__menu {
  opacity: 0;
  visibility: hidden;
  transition: all .2s;
}

.dropdown__item {
  cursor: pointer;
  align-items: center;
  padding: .8rem;
  transition: all .2s;
  display: flex;
}

.dropdown__item:not(:last-child) {
  border-bottom: 1px solid #3d3b5e;
}

.dropdown__item--title {
  color: #aeaeb7;
}

.dropdown__item--icon {
  fill: #fff;
  opacity: 0;
  width: 2rem;
  height: 2rem;
}

.dropdown__list {
  flex-direction: column;
  width: 100%;
  list-style: none;
  display: flex;
}

.dropdown__list--item {
  padding: .8rem;
  font-size: 1.6rem;
  display: flex;
}

.selected {
  background-color: #3d3b5e;
  border-radius: 9px;
}

.selected:after {
  content: "✔";
  margin-left: auto;
}

.active {
  opacity: 1;
  visibility: visible;
}

.search-bar {
  justify-content: center;
  align-items: center;
  gap: .4rem;
  display: flex;
}

@media only screen and (width <= 37.5rem) {
  .search-bar {
    flex-direction: column;
    gap: 1.2rem;
  }
}

.search-bar__icon {
  fill: #aeaeb7;
  z-index: 1;
  width: 2.4rem;
  height: 2.4rem;
  transition: all .2s;
  position: absolute;
  top: 1rem;
  left: 2.2rem;
}

.search-bar__box {
  justify-content: center;
  align-items: center;
  width: 45%;
  display: flex;
  position: relative;
}

@media only screen and (width <= 37.5rem) {
  .search-bar__box {
    width: 60%;
  }
}

.search-bar__input {
  color: #aeaeb7;
  background-color: #312f4b;
  border: none;
  border-radius: 9px;
  width: 95%;
  padding: 1.2rem 4.8rem;
  font-size: 1.6rem;
  transition: all .2s;
}

.search-bar__input::placeholder {
  color: #aeaeb7;
  font-family: DM Sans, sans-serif;
  font-size: inherit;
}

.search-bar__input:focus {
  outline: none;
  width: 100%;
}

.search-bar__input:focus + .search-bar__icon {
  left: 1rem;
}

.btn {
  cursor: pointer;
  color: inherit;
  background-color: #4455da;
  border: none;
  border-radius: 11px;
  padding: 1.2rem 1.8rem;
  font-family: DM Sans, sans-serif;
  font-size: 1.4rem;
}

@media only screen and (width <= 37.5rem) {
  .btn {
    width: 60%;
  }
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loading__dot {
  background-color: #aeaeb7;
  border-radius: 50%;
  width: 1.2rem;
  height: 1.2rem;
  animation: 1s linear infinite load;
  display: inline-block;
}

.loading__dot:first-child {
  animation-delay: 0s;
}

.loading__dot:nth-child(2) {
  animation-delay: .4s;
}

.loading__dot:nth-child(3) {
  animation-delay: .8s;
}

.loading p {
  font-family: DM Sans, sans-serif;
  font-size: 1.6rem;
}

.popup {
  z-index: 1000;
  backdrop-filter: blur(3rem);
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100vh;
  transition: all .4;
  font-family: DM Sans, sans-serif;
  font-size: 1.8rem;
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1);
}

.popup__content {
  color: #fff;
  background-color: #3d3b5e;
  border-radius: 9px;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 30%;
  padding: 2rem;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.popup__btn {
  cursor: pointer;
  background-color: inherit;
  border: none;
  width: 3.2rem;
  height: 3.2rem;
  position: absolute;
  top: 0;
  right: 1rem;
}

.popup__icon {
  fill: #fff;
  width: 3.2rem;
  height: 3.2rem;
}

.popup--active {
  opacity: 1;
  visibility: visible;
  transform: scale(1.2);
}

.header {
  color: #fff;
  grid-column: center-start / center-end;
  margin-top: 2rem;
}

.header__logo {
  align-items: center;
  gap: 1.4rem;
  display: flex;
}

.header__logo--img {
  width: 4.8rem;
  height: 4.8rem;
  animation: 2s linear infinite rotate;
}

.header__logo--text {
  letter-spacing: .01rem;
  margin-right: auto;
  font-size: 2rem;
}

.daily-forecast, .forecast-metrics, .hourly-forecast {
  font-family: DM Sans, sans-serif;
}

.country-forecast {
  color: #fff;
  background-image: linear-gradient(#312f4b);
  border-radius: 15px;
  grid-column: center-start / 5 col-end;
  justify-content: space-between;
  align-items: center;
  padding: 2.4rem;
  display: flex;
  position: relative;
}

@media only screen and (width <= 37.5rem) {
  .country-forecast {
    grid-area: 2 / center-start / auto / center-end;
  }
}

.country-forecast__country {
  font-family: DM Sans, sans-serif;
  font-size: 2rem;
}

.country-forecast__date {
  color: #d5d4d9;
  font-family: DM Sans, sans-serif;
  font-size: 1.2rem;
}

.country-forecast__temperature {
  font-size: 7.4rem;
  font-style: italic;
  font-weight: 200;
}

@media only screen and (width <= 68em) {
  .country-forecast__temperature {
    font-size: 5.2rem;
  }
}

.day {
  background-image: url("day1.70b65f9d.jpg");
  background-size: cover;
}

.night {
  background-image: url("night.1a7b99a6.jpg");
  background-size: cover;
}

.hourly-forecast {
  color: #fff;
  background-color: #272541;
  border-radius: 15px;
  flex-direction: column;
  grid-area: 2 / 6 col-start / 5 / center-end;
  gap: 1.2rem;
  max-height: 61rem;
  padding: 1.6rem;
  display: flex;
  overflow-y: auto;
}

@media only screen and (width <= 37.5rem) {
  .hourly-forecast {
    grid-area: 6 / center-start / auto / center-end;
  }
}

.hourly-forecast > :not(:first-child) {
  background-color: #312f4b;
  border: 1px solid #3d3b5e;
  border-radius: 15px;
  align-items: center;
  gap: 1.2rem;
  min-height: 6rem;
  padding: .4rem 1.2rem;
  display: flex;
}

.hourly-forecast__title {
  justify-content: space-between;
  align-items: center;
  gap: .8rem;
  font-family: inherit;
  font-weight: 500;
  display: flex;
}

.hourly-forecast__icon {
  fill: #fff;
  width: 2.4rem;
  height: 2.4rem;
}

.hourly-forecast__hour {
  margin-right: auto;
  font-size: 1.8rem;
}

.forecast-metrics {
  color: #d5d4d9;
  grid-area: 3 / center-start / auto / 5 col-end;
  grid-template-columns: repeat(auto-fit, minmax(14rem, 1fr));
  gap: 1.2rem;
  display: grid;
}

@media only screen and (width <= 51rem) {
  .forecast-metrics {
    grid-row: 3 / 5;
    align-items: center;
  }
}

@media only screen and (width <= 37.5rem) {
  .forecast-metrics {
    grid-column: center-start / center-end;
  }
}

.forecast-metrics > * {
  background-color: #312f4b;
  border: 1px solid #3d3b5e;
  border-radius: 15px;
  flex-direction: column;
  gap: 1.4rem;
  padding: 1.2rem 2.4rem;
  display: flex;
}

.forecast-metrics__value {
  color: #fff;
